import React, { Component } from 'react';
import styles from '../css/Section3.css';
import coverImage from '../images/cover.jpg';
import worldIcon from '../images/world.svg';
import barcodeImage from '../images/barcode.png';

export default class Section3 extends Component {
      render() {
            return (
                  <div className={styles.body}>
                        <img className={styles.cover} src={coverImage} alt="coverImage" />
                        <div className={styles.wrapper}>
                              <p className={styles.section1}>New<hr/>Solid<span className={styles.trademarksymbol}>®</span><img className={styles.worldIcon} src={worldIcon} alt="world"/></p>
                              <div className={styles.section2}>
                                    <div className={styles.l1}>
                                          <p className={styles.p1}>collections <hr/> 2.08</p>
                                          <p className={styles.p1}>:</p>
                                          <p>Actual New--s Product, Spring Summer</p>
                                          <p>collections / Street Boys / Justice / Velvet / Flexit</p>
                                          <span className={styles.span1}>0.2</span>
                                    </div>
                                    <div className={styles.l2}>
                                          <img src={barcodeImage} alt="..." />
                                    </div>
                                    <div className={styles.l3}>
                                          <span className={styles.topleft}>No.</span>
                                          <span className={styles.topright}>Travel Issue</span>
                                          <span className={styles.bottomleft}>#</span>
                                          <span className={styles.bottomright}>420</span>
                                    </div>
                              </div>
                              <div className={styles.section3}>
                                    <div className={styles.t1}>
                                          <span className={styles.t1s1}>DATE</span>
                                          <span className={styles.t1s2}>13/10/18</span>
                                    </div>
                                    <div className={styles.t2}>
                                          <p>Summer</p>
                                    </div>
                                    <div className={styles.t3}>
                                          <p>Collection</p>
                                    </div>
                              </div>
                              <div className={styles.section4}>
                                    <div className={styles.f1}>
                                          <div className={styles.f11}>U.S</div>
                                          <div className={styles.f12}>2-4 Peter St, Soho, London W1F 0AA,<br/> United Kingdom</div>
                                    </div>
                                    <div className={styles.f2}>
                                          <div className={styles.f11}>U.K</div>
                                          <div className={styles.f12}>1 First St NE, Washington, DC 20543,<br/>United State</div>
                                    </div>
                                    <div className={styles.f3}>
                                          <div className={styles.f11}>WEB</div>
                                          <div className={styles.f12}>www.newsolid-skateboarder.com</div>
                                    </div>
                                    <div className={styles.f4}>
                                          <div className={[styles.f11, styles.right].join(' ')}>MAIL</div>
                                          <div className={styles.f12}>Hello@newsolid-stateboarder.com</div>
                                    </div>
                              </div>
                        </div>
                  </div>
            )
      }
}



