import React, { Component } from 'react';
import styles from '../css/Section2.css';

export default class Section2 extends Component {
      render() {
            return (
                  <div className={styles.body}>
                        <div className={styles.wrapper}>
                              <p>
                                    <span className={styles.strong}>Gelion </span>
                                     is a Sans serif with a geometric touch with a minimal contrast of strokes, inspired by Futura, Avant Garde, Avenir and Neo-Grotesque Akzidenz Grotesk form remaining true to the gracefully geometric look of the early 20th-century typefaces, that ticks off a long list of branding requirements.
                              </p>
                        </div>
                  </div>
            )
      }
}



