import React, { Component } from "react";
import styles from "../css/Section6.css";
import back from "../images/back.svg";
import next from "../images/next.svg";

export default class Section6 extends Component {
  render() {
    return (
      <div className={styles.body}>
        <div className={styles.wrapper}>
          <div className={styles.navbar}>
            <h1>WilCicle®</h1>
            <ul>
              <li>
                <a href="#">Special Offer</a>
              </li>
              <li>
                <a href="#">Outlet</a>
              </li>
              <li>
                <a href="#">News</a>
              </li>
              <li>
                <a href="#">Featured</a>
              </li>
            </ul>
          </div>
          <h1>
            The Simplicity &#38;<br />Sportifity
          </h1>
          <div className={styles.btngroup}>
            <a className={styles.shopbtn} href="#">
            SHOP NOW
            </a>
            <div className={styles.pnbtn}>
              <a href="#">
                <img src={back} alt="..." />
              </a>
              <a href="#">
                <img src={next} alt="..." style={{marginLeft: "30px"}} />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



