import React, { Component } from 'react';
import styles from '../css/Top.css';

export default class Top extends Component {
      render() {
            return <div className={[styles.buytop, styles.clearfix].join(" ")}>
                        <div className={styles.buytoptitle}>
                              <span className={styles.toptext}>Buy</span>
                              <span className={[styles.topcolor, styles.leftmg].join(' ')}>
                              Gelion Typeface
                              </span>
                              <span className={[styles.toptext, styles.leftmg].join(' ')}>
                              ---75% OFF
                              </span>
                        </div>
                  </div>;
      }
}



