import React, { Component } from "react";
import styles from "../css/Section5.css";
import background from '../images/background_sec5.jpg';

export default class Section5 extends Component {
  render() {
    return <div className={styles.body}>
        <div className={styles.wrapper}>
          <div className={styles.content}>
            <div className={styles.content1}>
              <div className={styles.content1back}>
                <div className={styles.backsection1}>
                  <span>Exhibition &#38; Art Perfomance</span>
                  <span>Omega Presenting</span>
                </div>
                <div className={styles.backsection2}>
                  <img style={{ width: "100%" }} src={background} alt="background" />
                </div>
                <div className={styles.backsection3}>
                  <p>Room 24 - Rome Casa d'arte</p>
                  <p>Diagonal Recording and Studio, Rome</p>
                  <p>www.julienz-spacing.net</p>
                  <div className={styles.circlegroup}>
                    <span className={styles.circle} />
                    <span style={{ marginLeft: "20px" }} className={styles.circle} />
                  </div>
                </div>
                <div className={styles.backsection4}>
                  <span>more info hello@omega.com</span>
                  <span>@omega-Exhibition</span>
                </div>
              </div>
              <div className={styles.contenttext1}>
                Nello Stato Attuale Dell'interno
              </div>
            </div>
            <div className={styles.content2}>
              <div className={styles.contenttext2}>
                1.02.9. Booking --- 40© Fang---
                <br />&#38;<br /> <u>Royale!</u>
              </div>
            </div>
          </div>
        </div>
      </div>;
  }
}



