import React, { Component, Fragment } from "react";
import { animateScroll as scroll } from 'react-scroll';

import styles from "../css/App.css";

// Components..
import Top from "./Top";
import Section1 from "./Section1";
import Section2 from "./Section2";
import Section3 from "./Section3";
import Section4 from "./Section4";
import Section5 from "./Section5";
import Section6 from "./Section6";
import Footer from './Footer';

// images
import arrowImage from "../images/down-arrow.svg";

class App extends Component {
  state = {
    scrollArray: [974, 1461, 2638, 3604],
    scrollindex: 0,
    arrowClick: false
  };
  componentDidMount() {
    var arrowbtn = document.getElementById("arrow");
    var topbtn = document.querySelector("#top");

    topbtn.addEventListener("click", e => {
      scroll.scrollToTop({ duration: 1000 });
    });

    arrowbtn.addEventListener("click", e => {
      console.log(this.state.scrollindex);
      if (this.state.scrollArray.length === this.state.scrollindex) {
        scroll.scrollToBottom({ duration: 1500 });
        return;
      }
      scroll.scrollTo(this.state.scrollArray[this.state.scrollindex], { duration: 1000 });
      //window.scrollTo(0, this.state.scrollArray[this.state.scrollindex]);
      this.setState({
        scrollindex: this.state.scrollindex + 1
      });
    });
    window.onscroll = e => {
      //console.log(window.scrollY);
      console.log(this.state.scrollindex);
      for (let i = 0; i < this.state.scrollArray.length; i++) {
        if (window.scrollY < this.state.scrollArray[i]) {
          this.setState({
            scrollindex: i
          });
          return;
        } else {
          this.setState({
            scrollindex: this.state.scrollArray.length
          })
        }
      }
    };
  }
  render() {
    return (
      <Fragment>
        <Top />
        <Section1 />
        <Section2 />
        <Section3 />
        <Section4 />
        <Section5 />
        <Section6 />
        <Footer />
        <div className={styles.fixedarrow} id="arrow">
          <img src={arrowImage} alt="arrow" />
        </div>
        <div className={styles.fixedtop} id="top">
          TOP
        </div>
      </Fragment>
    );
  }
}

export default App;



