import React, { Component } from 'react';
import styles from '../css/Section1.css';

export default class Section1 extends Component {
      render() {
            return <div className={styles.body}>
                <div className={styles.wrapper}>
                  <div className={styles.maintitle}>
                    <span
                      className={[styles.topleft, styles.bold].join(
                        " "
                      )}
                    >
                      Letter Omega Foundry
                    </span>
                    <span
                      className={[
                        styles.topright,
                        styles.bold
                      ].join(" ")}
                    >
                      Copyright©2018
                    </span>
                    <div className={styles.maintitlecenter}>
                      <span>Gelion</span>
                    </div>
                    <span
                      className={[
                        styles.bottomleft,
                        styles.bold
                      ].join(" ")}
                    >
                      A Geometric Sans Serif
                    </span>
                    <span
                      className={[
                        styles.bottomright,
                        styles.bold
                      ].join(" ")}
                    >
                      Designed by Doni Sukma
                    </span>
                  </div>
                  <div className={styles.footer}>
                    <div className={styles.footertext}>
                      · BALANCE · MODERNIST · CLEAN · GEOMETRIC ·
                      SIMPLICITY · CONTEMPORARY ·
                    </div>
                  </div>
                </div>
              </div>;
      }
}



