import React, { Component } from 'react';
import styles from '../css/Footer.css';

export default class Footer extends Component {
      render(){
            return (
                  <div className={styles.body}>
                        <div className={styles.wrapper}>
                              <div className={styles.section}>
                                    <div className={styles.title}>
                                          Become Backer
                                    </div>
                                    <div className={styles.content}>
                                          Support the community and keep development going strong.
                                    </div>
                                    <button className={styles.leftbtn}>DONATE</button>
                              </div>
                              <div className={styles.section}>
                                    <div className={styles.title}>
                                          Let's get social
                                    </div>
                                    <div className={styles.content}>
                                          Follow my online page on Facebook and GitHub.
                                    </div>
                                    <div className={styles.icons}>
                                          <a href="#" class="fa fa-facebook-square" ></a>
                                          <a href="#" style={{marginLeft: "15px"}} class="fa fa-github-square"></a>
                                    </div>
                              </div>
                        </div>
                  </div>
            );
      }
}


